// @mui
import { alpha } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";

// ----------------------------------------------------------------------
export default function Help() {

  return (
    <Box sx={{ px: { xs: 2, md: 4 } }}>
      <Typography variant="h4"> Help </Typography>
      <Box
        sx={{
          mt:1,
          width:'100%',
          height:'80vh',
          borderRadius: 2,
          bgcolor: (theme) => alpha(theme.palette.grey[500], 0.04),
          border: (theme) => `dashed 1px ${theme.palette.divider}`,
        }}
      >
        <iframe
          title="Help Video or Guide"
          loading="lazy"
          onError={() => console.error("Failed to load help video.")}
          style={{
            width: "100%",
            height: "100%",
            border: "none",
            padding: 0,
            margin: 0,
          }}
          src="https://www.canva.com/design/DAGkdleS6Fw/Y8jmTMfqw3171r5gK9nRKw/view?embed"
          allowFullScreen
        />
      </Box>
    </Box>
  );
}
